.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 550px;
    height: 500px;
    margin-top: 100px;
    background-color: #fff;
    padding-bottom: 40px;
    border-radius: 20px;
}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-top: 30px;
    width: 100%;
}
.header .text{
    color: #3c009d;
    font-size: 48px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    
}
.underline{
    width: 61px;
    height:6px;
    background-color: #3c009d;
    border-radius: 9px;
    
}
.inputs{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    

}
.inputs .input{
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 50px;
    background-color: lightgray;
    border-radius: 6px;
}
 .input img{
    margin: 0 30px;
}
.input input{
    width: 100%;
    height: 50px;
    border: none;
    outline: none;
    color: #797979;
    background: transparent;
    font-size: 22px;
}
.forget-password{
    display: flex;
    margin-top: 20px;
    margin-left: 60px;
    font-size: 18px;
    font-family: "Poppins", sans-serif;
    font-weight: 400;
}
.forget-password a{
    text-decoration: none;
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    
}
.container-submit{
    display: flex;
    margin-top: 46px;
    justify-content: space-evenly;
    
}
.container-submit .submit{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3c009d;
    width: 200px;
    height: 60px;
    border-radius: 30px;
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
    
}
.submit.gray{
    background-color: #EAEAEA;
    color: #676767;
}